import { Call, Email, LocationOn } from "@material-ui/icons";
import styled from "styled-components";
import Announcements from "../components/Announcements";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import NewsLetter from "../components/NewsLetter";

const Container = styled.div``;

const Wrapper = styled.div`
  background-color: #F9F3EE;
  padding: 50px;
  display: flex;
`;

const ImgContainer = styled.div`
  flex: 1;
`;

const Image = styled.img`
  width: 80%;
  height: 80vh;
  object-fit: cover;
`;

const InfoContainer = styled.div`
  flex: 1;
  padding: 0px 50px;
`;

const Title = styled.h1`
  font-weight: 600;
`;

const Desc = styled.p`
  margin: 20px 0px;
`;

const Seller = styled.span`
  font-weight: 500;
  font-size: 20px;
`;

const FilterContainer = styled.div`
  width: 50%;
  margin: 30px 0px;
  display: flex;
  justify-content: space-between;
`;

const Filter = styled.div`
  display: flex;
  align-items: center;
`;

const FilterTitle = styled.span`
  font-size: 20px;
  font-weight: 200;
`;

const FilterSize = styled.select`
  margin-left: 10px;
  padding: 5px;
`;

const FilterSizeOption = styled.option``;

const SellerDetails=styled.div`
  margin-top: 40px;
  align-items: center;
  line-height: 23px;
`
const Phone=styled.div`
  display: flex;
  align-items: center;
`
const Adress=styled.div`
  display: flex;
  align-items: center;
`
const Mail=styled.div`
  display: flex;
  align-items: center;
`



const ProductP10 = () => {
  return (
    <Container>
      <Navbar />
      <Announcements />
      <Wrapper>
        <ImgContainer>
          <Image src="https://i.ibb.co/LzjhZSP/p10.png"/>
        </ImgContainer>
        <InfoContainer>
          <Title>BAMBOO HAMMOCK SWING</Title>
          <Desc>
            Premium quality Hammock Swing.
          </Desc>
          <Seller>SELLER: JK Bamboo artwork</Seller>
          <FilterContainer>
            <Filter>
              <FilterTitle>Size</FilterTitle>
              <FilterSize>
                <FilterSizeOption>Large</FilterSizeOption>
              </FilterSize>
            </Filter>
          </FilterContainer>
          <SellerDetails>
          Seller Details: <b>JK Bamboo artwork.</b> Kolkata, WB <br></br>
          <Phone><Call/> Call:+33 4561 1234 <br></br></Phone>
          <Adress><LocationOn/> Address : Rose street,Newtown-700147 <br></br></Adress>
          <Mail><Email/> E-mail: jkart@gmail.com</Mail>
          </SellerDetails>
        </InfoContainer>
      </Wrapper>
      <NewsLetter />
      <Footer />
    </Container>
  );
};

export default ProductP10;