import { Link } from "react-router-dom";
import styled from "styled-components";



const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.4),
      rgba(255, 255, 255, 0.3)
    ),
    url("https://thumbs.dreamstime.com/b/vietnamese-consumer-choice-product-open-air-farmer-market-dalat-vietnam-jan-colorful-terracotta-as-vase-jar-phial-to-decorative-40655428.jpg")
      center;
 
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Wrapper = styled.div`

  width: 20%;
  padding: 20px;
  background-color: white;
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 600;
`;

const Form = styled.form`

  display: flex;
  flex-direction: column;
`;

const Input = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 10px 0px;
  padding: 10px;
`;


const Button = styled.button`
  display: flex;
  align-items: center;
  width: 20%;
  height: 1vh;
  border: none;
  margin: 20px  0px 10px;
  padding: 15px 20px;
  background-color: #54BAB9;
  color: white;
  cursor: pointer;
`;

const L=styled.a`
  margin: 5px 0px;
  font-size: 12px;
  text-decoration: underline;
  cursor: pointer;

`

const Login = () => {
  return (
    <Container>
      <Wrapper>
        <Title>LOGIN</Title>
        <Form>
          <Input placeholder="Username" />
          <Input placeholder="Password" />
          <Button><Link to='/Home' style={{textDecoration:'none' , color:'black'}}>LOGIN</Link></Button>
          <L>Forgot Password?</L>
          <L>CREATE A NEW ACCOUNT</L>
        </Form>
        
      </Wrapper>
    </Container>
  );
};

export default Login;