import { Link } from 'react-router-dom';
import styled from 'styled-components';

const Container=styled.div`
    flex: 1;
    margin: 3px 3px 0;
    position: relative;
    min-width: 280px;
    height: 350px;
`
const Image=styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
`
const Info=styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`
const Title=styled.h3`
    color: white;
    margin-bottom: 15px;
`
const Button=styled.button`
    padding: 5px;
    font-size: 15px;
    font-weight: 700;
    background-color: white;
    color: black;
    border-color: gray;
    cursor: pointer;
`

const CategoryItem = ({item}) => {
    return <Container>
            <Image src={item.img}/>
            <Info>
                <Title>{item.title}</Title>
                <Button><Link to={item.l} style={{textDecoration:'none' , color:'black'}}>CONTACT SUPPLIER</Link></Button>
            </Info>
        </Container>;
    
};

export default CategoryItem; 