import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Routes , Route } from "react-router-dom";
import App from './App';
import Home from "./pages/Home";
import ProductP2 from "./pages/ProductP2";
import ProductP3 from "./pages/ProductP3";
import ProductP4 from "./pages/ProductP4";
import ProductP5 from "./pages/ProductP5";
import ProductP6 from "./pages/ProductP6";
import ProductP7 from "./pages/ProductP7";
import ProductP8 from "./pages/ProductP8";
import ProductP9 from "./pages/ProductP9";
import ProductP10 from "./pages/ProductP10";
import ProductP11 from "./pages/ProductP11";
import ProductP12 from "./pages/ProductP12";
import ProductP13 from "./pages/ProductP13";
import Teracotta from './pages/Teracotta';
import Paintings from './pages/Paintings';
import Bangles from './pages/Bangles';
import BambooJute from './pages/BambooJute';
import Dress from './pages/Dress';
import Terms from './pages/Terms';
import Register from './pages/Register';
import Login from './pages/Login';
import ScrollToTop from './pages/ScrollToTop';

ReactDOM.render(
    <BrowserRouter>
      <ScrollToTop/>
        <Routes>
          <Route path="/" element={<App/>} />
          <Route path="Home" element={<Home/>} />
          <Route path="ProductP2" element={<ProductP2/>} />
          <Route path="ProductP3" element={<ProductP3/>} />
          <Route path="ProductP4" element={<ProductP4/>} />
          <Route path="ProductP5" element={<ProductP5/>} />
          <Route path="ProductP6" element={<ProductP6/>} />
          <Route path="ProductP7" element={<ProductP7/>} />
          <Route path="ProductP8" element={<ProductP8/>} />
          <Route path="ProductP9" element={<ProductP9/>} />
          <Route path="ProductP10" element={<ProductP10/>} />
          <Route path="ProductP11" element={<ProductP11/>} />
          <Route path="ProductP12" element={<ProductP12/>} />
          <Route path="ProductP13" element={<ProductP13/>} />
          <Route path="Teracotta" element={<Teracotta/>} />
          <Route path="Paintings" element={<Paintings/>} />
          <Route path="Bangles" element={<Bangles/>} />
          <Route path="BambooJute" element={<BambooJute/>} />
          <Route path="Dress" element={<Dress/>} />
          <Route path="Terms" element={<Terms/>} />
          <Route path="Register" element={<Register/>} />
          <Route path="Login" element={<Login/>} />

        </Routes>
    </BrowserRouter>,
  document.getElementById('root')
);