import React, { useRef } from 'react';
import emailjs from '@emailjs/browser'
import styled from "styled-components";

const Container=styled.div`
    height: 45vh;
    background-color: #CDF0EA;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`
const Title=styled.h3`
    font-size: 50px;
    margin-bottom: 15px;
    font-weight: 700;
`
const Description=styled.div`
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 15px;
`
const Inputcntr=styled.div`
    width: 400px;
  form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    font-size: 16px;
    input {
      width: 100%;
      height: 30px;
      padding: 7px;
      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(220, 220, 220);
      &:focus {
        border: 2px solid rgba(0, 206, 158, 1);
      }
    }
    textarea {
      max-width: 100%;
      min-width: 100%;
      width: 100%;
      max-height: 80px;
      min-height: 80px;
      padding: 7px;
      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(220, 220, 220);
      &:focus {
        border: 2px solid rgba(0, 206, 158, 1);
      }
    }
    label {
      margin-top: 1rem;
    }
    input[type="submit"] {
      margin-top: 2rem;
      cursor: pointer;
      background: #6EDCD9;
      color: black;
      font-weight: 600;
      font-size: 17px;
      border: none;
    }
  }
`
const Input=styled.input`
    border: none;
    flex: 8;
`
const Button=styled.button`
    flex: 1;
    background-color: #54BAB9;
    color: white;
    border-color: gray;
    border-width: 2px;
    cursor: pointer;
`
const NewsLetter = () => {
    const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_ansh', 'template_nvt7teq', form.current, 'iL5u0pCrketAUlIoH')
      .then((result) => {
          console.log(result.text);
          console.log("messaage sent");
      }, (error) => {
          console.log(error.text);
      });
  };
    return (
        <Container>
            <Title>Newsletter</Title>
            <Description>Never miss any update from your Favourite products ! SUBSCRIBE.</Description>
            <Inputcntr>
            <form ref={form} onSubmit={sendEmail}>
            <label>Email</label>
            <input type="email" name="user_email" required />
            <label>Feedback (if any)</label>
            <textarea name="message" />
            <input type="submit" value="Subscribe" />
            </form>
            </Inputcntr>
        </Container>
    )
}



export default NewsLetter;