import { Call, Email, LocationOn } from "@material-ui/icons";
import styled from "styled-components";
import Announcements from "../components/Announcements";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import NewsLetter from "../components/NewsLetter";


const Container=styled.div`

`
const Wrapper=styled.div`
     width: 100vw;
     height: 45vh;
     background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.8)
    ),
    url("https://images.pexels.com/photos/3048077/pexels-photo-3048077.jpeg?auto=compress&cs=tinysrgb&w=1600")
      center;
    margin: 0;
    padding: 55px 6px;
    font-weight: 600;
`
const Title=styled.h1`
    display: flex;
    align-items: center;
    justify-content: center;
`
const SellerDetails=styled.div`
  margin: 0 42%;
  margin-top: 10px;
  align-items: center;
  line-height: 23px;
`
const Phone=styled.div`
  display: flex;
  align-items: center;
`
const Adress=styled.div`
  display: flex;
  align-items: center;
`
const Mail=styled.div`
  display: flex;
  align-items: center;
`
const Bangles = () => {
    return (<Container> 
    <Navbar/>
    <Announcements/>
  <Wrapper>
  <Title>CONCH SHELL BANGLES</Title>
  <SellerDetails>
  Seller Details: <b>Conch Shell Bangles Retail pvt. ltd.</b> Kolkata, WB <br></br>
  <Phone><Call/> Call:+33 1472 2583 <br></br></Phone>
  <Adress><LocationOn/> Address : 23A ,Kalakar Street-700144 <br></br></Adress>
  <Mail><Email/> E-mail: conchbangles@gmail.com</Mail>
  </SellerDetails>
  </Wrapper>
  <NewsLetter/>
  <Footer/>
</Container>
    );
}


export default Bangles;