import { Link } from 'react-router-dom';
import { SearchOutlined } from '@material-ui/icons';
import styled from 'styled-components';
const Info=styled.div`
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #15e4d216;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    cursor: pointer;
`

/* We will write hover effect in Container and gonna use for both
   Container and Icon, so for this we have to define Info beforehand. */

const Container=styled.div`
    display: flex;
    flex: 1;
    margin: 5px 5px 0;
    min-width: 280px;
    height: 350px;
    align-items: center;
    justify-content: center;
    background-color: #F9F3EE;
    position: relative;

    &:hover ${Info}{
        opacity: 1;
    }
`
const Circle=styled.div`
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: white;
    position: absolute;

`
const Image=styled.img`
    margin-right: 8px;
    height: 75%;
    z-index: 2;
`

const Icon=styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 7px;
    cursor: pointer;
    transition: all 0.3s ease;
     &:hover{
        background-color: #CDF0EA;
        transform: scale(1.1);
    };
`


const Product = ({item}) => {
    return <Container>
            <Circle/>
            <Image src={item.img} alt={"Product"}/>
            <Info>
                <Icon>
                    <Link to={item.l}><SearchOutlined/></Link>
                    
                </Icon>
            </Info>
    </Container>;
}


export default Product;