import { Link } from 'react-router-dom';
import { Badge } from '@material-ui/core';
import { Favorite, LanguageOutlined, Search } from '@material-ui/icons';
import React from 'react';
import styled from 'styled-components';

const Container=styled.div`
      height: 60px;
`;
const Wrapper=styled.div`
      padding: 10px 20px;
      display:flex;
      justify-content: space-between;
      align-items: center;
      overflow-x: auto;
      white-space: nowrap;
`;

const Left=styled.div`
  flex:1;
  display: flex;
  align-items: center;
`;
const Language=styled.span`
    display: flex;
    font-size: 15px;
    align-items: center;
    
`;
const SearchContainer=styled.div`
  border: 0.7px solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 20px;
  padding: 4px;
`;
const Input=styled.input`
  border: none;
`

const Center=styled.div`
  flex: 1;
  text-align: center;
`;
const Logo=styled.h1`
  font-weight: bold;
`;
const Right=styled.div`
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: flex-end;
`;
const MenuItem=styled.div`
  font-size: 14px;
  cursor: pointer;
  margin-left: 30px;
`;


        

const Navbar = () => {
    return  ( <Container>
                <Wrapper>
                  <Left>
                    <Language>
                      EN
                      <LanguageOutlined/>
                    </Language>
                    <SearchContainer>
                      <Input/>
                      <Search style={{color:"blue",fontSize:16,cursor:"pointer"}}/>
                    </SearchContainer>
                  </Left>
                  <Center>
                    <Logo>
                      LocalKart.
                    </Logo>
                  </Center>
                  <Right>
                    <MenuItem><Link to='/Register' style={{textDecoration:'none' , color:'black'}}>REGISTER</Link></MenuItem>
                    <MenuItem><Link to='/Login' style={{textDecoration:'none' , color:'black'}}>SIGN IN</Link></MenuItem>
                    <MenuItem>
                    <Badge badgeContent={2} color="primary">
                        <Favorite/>
                    </Badge>
                    </MenuItem>
                  </Right>
                </Wrapper>
              
              </Container>
            )
}

export default Navbar;